module.exports = "<h1 id=\"react-github-corner\">react-github-corner</h1>\n<p><a href=\"http://badge.fury.io/js/react-github-corner\"><img src=\"https://badge.fury.io/js/react-github-corner.svg\" alt=\"NPM version\"></a>\n<a href=\"https://david-dm.org/skratchdot/react-github-corner\"><img src=\"https://david-dm.org/skratchdot/react-github-corner.svg\" alt=\"Dependency Status\"></a>\n<a href=\"https://david-dm.org/skratchdot/react-github-corner#info=devDependencies\"><img src=\"https://david-dm.org/skratchdot/react-github-corner/dev-status.svg\" alt=\"devDependency Status\"></a></p>\n<p><a href=\"https://npmjs.org/package/react-github-corner\"><img src=\"https://nodei.co/npm/react-github-corner.png\" alt=\"NPM\"></a></p>\n<h2 id=\"description\">Description</h2>\n<p>Add a Github banner to your project page. A React version of:</p>\n<ul>\n<li><a href=\"https://github.com/tholman/github-corners\">https://github.com/tholman/github-corners</a></li>\n</ul>\n<h2 id=\"getting-started\">Getting Started</h2>\n<p>Install the module with: <code>npm install --save react-github-corner</code></p>\n<h2 id=\"usage\">Usage</h2>\n<pre><code class=\"language-javascript\">import React, { Component } from &#39;react&#39;;\nimport GithubCorner from &#39;react-github-corner&#39;;\nexport default class MyApp extends Component {\n  render() {\n    return (\n      &lt;div&gt;\n        &lt;header&gt;Wow&lt;/header&gt;\n        &lt;div&gt;Cool&lt;/div&gt;\n        &lt;footer&gt;Neat&lt;/footer&gt;\n        &lt;GithubCorner href=&quot;https://github.com/username/repo&quot; /&gt;\n      &lt;/div&gt;\n    );\n  }\n}</code></pre>\n<h2 id=\"documentation\">Documentation</h2>\n<p>Here are the props you can pass to the <code>GithubCorner</code> instance:</p>\n<table>\n<thead>\n<tr>\n<th align=\"center\">Property Name</th>\n<th align=\"center\">Type</th>\n<th align=\"center\">Default Value</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody><tr>\n<td align=\"center\">href</td>\n<td align=\"center\">String</td>\n<td align=\"center\">&#39;/&#39;</td>\n<td>The link to your project page</td>\n</tr>\n<tr>\n<td align=\"center\">size</td>\n<td align=\"center\">Number or String</td>\n<td align=\"center\">80</td>\n<td>The width and height of the banner</td>\n</tr>\n<tr>\n<td align=\"center\">direction</td>\n<td align=\"center\">String</td>\n<td align=\"center\">&#39;right&#39;</td>\n<td>Whether the banner shows up on the right or left</td>\n</tr>\n<tr>\n<td align=\"center\">octoColor</td>\n<td align=\"center\">String</td>\n<td align=\"center\">&#39;#fff&#39;</td>\n<td>The CSS color of the Octocat</td>\n</tr>\n<tr>\n<td align=\"center\">bannerColor</td>\n<td align=\"center\">String</td>\n<td align=\"center\">&#39;#151513&#39;</td>\n<td>The CSS color of the banner</td>\n</tr>\n<tr>\n<td align=\"center\">ariaLabel</td>\n<td align=\"center\">String</td>\n<td align=\"center\">&#39;Open GitHub project&#39;</td>\n<td>The aria-label for a11y support</td>\n</tr>\n<tr>\n<td align=\"center\">className</td>\n<td align=\"center\">String</td>\n<td align=\"center\">undefined</td>\n<td>Additional class names to be merged with the <code>github-corner</code> default</td>\n</tr>\n<tr>\n<td align=\"center\">svgStyle</td>\n<td align=\"center\">Object</td>\n<td align=\"center\">undefined</td>\n<td>Custom styles to apply to the main <code>svg</code> element</td>\n</tr>\n</tbody></table>\n<p>Any additional props will be added to the <code>&lt;a /&gt;</code> tag that is rendered.\nFor instance, you can do:</p>\n<pre><code>&lt;GithubCorner style=&quot;display:none&quot; /&gt;</code></pre><p>and the <code>style</code> attribute will be rendered (which will hide the element).</p>\n<h2 id=\"links\">Links</h2>\n<ul>\n<li><a href=\"https://github.com/skratchdot/react-github-corner/\">Source Code</a></li>\n<li><a href=\"http://projects.skratchdot.com/react-github-corner/\">Project Page</a></li>\n<li><a href=\"https://github.com/skratchdot/react-github-corner/tree/gh-pages\">Project Page Source</a></li>\n</ul>\n<h2 id=\"license\">License</h2>\n<p>Copyright (c) 2015 <a href=\"http://skratchdot.com/\">skratchdot</a><br>Licensed under the <a href=\"LICENSE-MIT\">MIT license</a>.</p>\n";

